import type { SourceSelectorSubmissionV2 } from "@rp/react/tanstack-api/content"
import { SourceSelectorSelectedSourceInfoV2 } from "../sourceSelector.types"
import { SourceSelectorSelectedCategoryInfo } from "../sourceSelector.types"

type HandleApplyClickFuncProps = {
  isFilterInclude: boolean
  selectedSources: Array<SourceSelectorSelectedSourceInfoV2>
  selectedCategories: Array<SourceSelectorSelectedCategoryInfo>
  applyCallback: (sourceSelectionSubmission: SourceSelectorSubmissionV2) => void
}

function handleApplyClickFuncV2({
  isFilterInclude,
  selectedSources,
  selectedCategories,
  applyCallback,
}: HandleApplyClickFuncProps): void {
  const sourceIDs = selectedSources.map(source => source.id)
  const categoryIDs = selectedCategories.map(category => category.id)

  // TODO: optimize sources already accounted for in categories
  // TODO: optimize all sources in a category

  applyCallback({ isFilterInclude, sourceIDs, categoryIDs })
}

export { handleApplyClickFuncV2 }
